import React from 'react';
import foods from '../foods.json';
import FoodBox from '../components/alimentos/Foodbox';
import AddFood from '../components/alimentos/AddFood';
import SearchBar from '../components/alimentos/SearchBar';

class Alimentos extends React.Component {
    state = {
        foods: foods,
        displayedFoods: [...foods]
      }
    
      AddFoodhandle = (newFoodItem) => {
        const newFoods = [newFoodItem, ...this.state.foods]
        this.setState({
          foods: newFoods,
          displayedFoods: newFoods
        })
      }
    
      handleFilterFood = (value) => {
        const newFoods = this.state.foods.filter(food => (food.name.toUpperCase().includes(value.toUpperCase())))
        this.setState({
          displayedFoods: newFoods
        })
      }
    
      render() {
        const foodList = this.state.displayedFoods.map((element) => {
          return <FoodBox key={element.name} food={element} />
        })
    
        return (
          <div className="App">
    
            <SearchBar filterFood={this.handleFilterFood} />
    
            <AddFood addFood={this.AddFoodhandle} />
            {foodList}
    
    
          </div>
    );
  }
}

export default Alimentos;